$boxSize: 400px;
$sizePoint: $boxSize / 20;
$posPoint: $boxSize / 2 - $sizePoint / 2;
$borderRadius: 10px;

$hightMin: $boxSize / 3;
$widthMin:  $boxSize / 30;
$posMinLeft: $boxSize / 2 - $widthMin / 2;
$posMinTop: $boxSize / 2 - $hightMin;

$hightSec:  $boxSize / 2;
$widthSec: $boxSize / 33;
$posSecLeft: $boxSize / 2 - $widthSec / 2;
$posSecTop: $boxSize / 2 - $hightSec;
